/* ------------------------------------ *\
    #CLEARFIX
\* ------------------------------------ */

/**
 * Micro clearfix, as per:
 * css-101.org/articles/clearfix/latest-new-clearfix-so-far.php
 * Extend the clearfix class with Sass to avoid the `.clearfix` class appearing
 * over and over in your markup.
 */
.clearfix {

    &::after {
        display: table;
        clear: both;
        content: "";
    }

}
